	module.exports = {"data":{"markdownRemark":{"html":"<h2>Law of Cosines</h2>\n<p>The Law of Cosines can be used to find the ramaining sides or angles of a non-right triangle when given either the length of two sides and its included angle (SAS) or the lengths of the three sides (SSS).\nThe Law of Cosines states:\n<img src=\"http://hyperphysics.phy-astr.gsu.edu/hbase/imgmth/lcos.gif\"></p>\n<p>This looks very similar to the Pythagorean Theorem, and if C was 90 degrees (a right triange), the expression acctually simplifies to the Pythagorean Thoerem.  So, the Pythagorean Theorem is a special case of the Law of Cosines.</p>\n<p>You can also switch the angle C with either A or B, but make sure to change the respective side (c) to a or b.</p>\n<img src=\"http://www.mathwarehouse.com/trigonometry/images/law-of-cosines/law-of-cosines-formula-and-picture2.png\">\n<p>Ex:</p>\n<img src=\"http://www.mathwarehouse.com/sheets/trigonometry/advanced/law-of-sines-and-cosines/images/picture-of-law-of-cosines.png\">\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href=\"https://www.varsitytutors.com/hotmath/hotmath_help/topics/law-of-cosines\">https://www.varsitytutors.com/hotmath/hotmath_help/topics/law-of-cosines</a>\n<a href=\"http://mathworld.wolfram.com/LawofCosines.html\">http://mathworld.wolfram.com/LawofCosines.html</a></p>","fields":{"slug":"/mathematics/law-of-cosines/"},"frontmatter":{"title":"Law of Cosines"}}},"pathContext":{"slug":"/mathematics/law-of-cosines/"}}

